import React from "react"
import { graphql } from "gatsby"
import {
  Header,
  Container,
  Parallax,
  Paragraph,
  Row,
  Col,
  Heading,
  Center,
} from "../components/index"

export default ({ data: { banner } }) => (
  <>
    <Header />
    <Center>
      <Heading>Chapter Two</Heading>
      <h1>Out of the Pan</h1>
    </Center>
  </>
)

export const query = graphql`
  query {
    banner: file(relativePath: { eq: "forside-tittellogo.png" }) {
      childImageSharp {
        fluid(quality: 90, maxWidth: 1080) {
          ...GatsbyImageSharpFluid_withWebp
        }
      }
    }
  }
`
